html{position:relative;min-height:100%
}
body #app-wrapper{font-family:'Nunito', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100vh;width:100vw;position:relative;top:0;left:0;z-index:0;overflow:hidden
}
body #app-wrapper .no-pad{padding:0
}
body #app-wrapper .container-fluid{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
body #app-wrapper .map-wrapper{height:100%;z-index:1
}
body #app-wrapper .map-wrapper #map{height:100%;min-height:calc(100vh - 86px);width:100%
}
body #app-wrapper .sidebar-wrapper{background:rgba(255,255,255,0.75);height:100%;z-index:5;top:0;right:0
}
body .footer{position:absolute;bottom:0;width:100%;height:60px;line-height:60px;background-color:#f5f5f5
}
.weather-situation,.wind-situation{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.weather-situation .icon-wrapper,.wind-situation .icon-wrapper{width:100px;text-align:center;display:inline-block
}
.weather-situation span,.wind-situation span{padding-left:1rem
}
input[type="range"]{height:40px;width:100%
}
label{display:block
}
@media (min-width: 576px){
html .weather-situation,html .wind-situation{font-size:3rem
}
html .weather-situation .icon-wrapper,html .wind-situation .icon-wrapper{width:100px;font-size:4rem
}
}
@media (min-width: 768px){
html .weather-situation,html .wind-situation{font-size:2rem
}
html .weather-situation .icon-wrapper,html .wind-situation .icon-wrapper{width:100px;font-size:3rem
}
}
@media (min-width: 992px){
html .weather-situation,html .wind-situation{font-size:3rem
}
html .weather-situation .icon-wrapper,html .wind-situation .icon-wrapper{width:100px;font-size:4rem
}
}
#local-weather-wrapper{background:rgba(255,255,255,0.5)
}
#local-weather-wrapper .weather-situation i,#local-weather-wrapper .wind-situation i{transition:all 1s ease
}
#local-weather-wrapper table{text-align:left
}
#local-weather-wrapper table i{width:1.5rem
